.banner {
	background-color: $color-primary;
	background-image: url('../img/bg-banner.png');
	padding: 40px 0;

	&__content {
		@include jcsb;

		@media (max-width: 991px) {
			flex-direction: column;
			gap: 20px;
			text-align: center;
		}
	}

	&__title {
		font-family: 'Nunito';
		font-size: 32px;
		line-height: calc(40 / 32);
		font-weight: 700;
		color: #fff;

		@media (min-width: 992px) {
			max-width: 300px;
		}

		@media (max-width: 1160px) {
			font-size: 26px;
		}

		@media (max-width: 575px) {
			font-size: 20px;
		}
	}

	&__text {
		position: relative;
		color: #a9b3c8;

		@media (min-width: 992px) {
			max-width: 540px;
			padding: 0 30px 0 70px;
		}

		&::before {
			@media (min-width: 992px) {
				content: '';
				width: 4px;
				height: 100%;
				background: #9ca6bc;
				border-radius: 5px;
				position: absolute;
				left: 25px;
				top: 3px;
			}
		}
	}

	&__btn {
		border: 1px solid transparent;

		&:hover {
			border-color: #fff;
		}
	}
}