.form {
	max-width: 490px;
	margin: -100px 15px 0 15px;
	padding: 30px;
	background-color: #fff;
	border: 1px solid #c3c3c3;
	border-radius: 5px;

	@media (min-width: 576px) {
		margin-left: auto;
		margin-right: auto;
	}

	&__body {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30px;

		@media (max-width: 575px) {
			grid-template-columns: 1fr;
		}
	}

	&__input {
		border-bottom: 1px solid #b9b9b9;
		color: #b9b9b9;
		outline: none;
		padding-bottom: 15px;

		&::placeholder {
			color: #b9b9b9;
		}
	}

	&__textarea {
		width: 100%;
		border-bottom: 1px solid #b9b9b9;
		color: #b9b9b9;
		outline: none;
		padding-bottom: 15px;

		@media (min-width: 576px) {
			grid-column: 1/3;
		}

		&::placeholder {
			color: #b9b9b9;
		}
	}

	&__bottom {
		margin-top: 20px;
		text-align: center;
	}

	&__btn {
		background-color: #b9b9b9;
		color: #fff;
		transition: all 0.3s ease;

		&:hover {
			color: #fff;
		}
	}
}