.intro {
	background: linear-gradient(90deg, rgba(219, 226, 228, 1) 0%, rgba(196, 213, 228, 1) 35%, rgba(196, 213, 228, 1) 100%);

	&__content {
		position: relative;
		padding: 216px 0;

		@media (max-width: 1160px) {
			padding: 100px 0;
		}

		@media (max-width: 991px) {
			padding: 60px 0 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			gap: 30px;
		}

		@media (max-width: 575px) {
			padding-top: 30px;
		}
	}

	&__info {
		max-width: 700px;

		@media (min-width: 1530px) {
			max-width: 710px;
		}

		@media (max-width: 1160px) {
			max-width: 500px;
		}
	}

	&__desc {
		font-size: 24px;
		font-weight: 300;
		letter-spacing: -0.025em;
		color: #556382;

		@media (max-width: 1160px) {
			font-size: 20px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__title {
		margin-top: 30px;
		font-family: 'Nunito';
		font-weight: 800;
		font-size: 46px;
		line-height: calc(61 / 46);
		text-transform: uppercase;
		letter-spacing: -0.025em;
		color: $color-primary;

		@media (max-width: 1160px) {
			font-size: 36px;
			margin-top: 15px;
		}

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__text {
		font-family: 'Nunito';
		font-weight: 800;
		font-size: 25px;
		line-height: calc(61 / 46);
		text-transform: uppercase;
		letter-spacing: -0.025em;
		color: $color-primary;
		margin-top: 20px;

		@media (max-width: 1160px) {
			font-size: 22px;
			margin-top: 15px;
		}

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__btn {
		margin-top: 40px;

		@media (max-width: 575px) {
			margin-top: 20px;
		}
	}

	&__img {
		@media (min-width: 992px) {
			position: absolute;
			bottom: -4px;
			right: 0;
		}

		@media (min-width: 1530px) {
			right: -197px;
		}

		@media (max-width: 991px){
			transform: translateY(4px);
		}

		img {
			@media (max-width: 1160px) {
				width: 441px;
				height: 450px;
			}

			@media (max-width: 480px) {
				width: 290px;
				height: 295px;
			}
		}
	}

	&__name {
		position: absolute;
		bottom: 35px;
		right: 0;
		display: inline-block;
		border-radius: 9px;
		background-color: $color-primary;
		padding: 20px;
		text-align: center;

		@media (max-width: 1529px) {
			right: 180px;
		}

		@media (max-width: 991px) {
			right: 0;
			left: 50%;
			transform: translateX(-50%);
		}

		@media (max-width: 575px) {
			padding: 10px;
			bottom: 15px;
		}

		@media (max-width: 360px) {
			width: 100%;
		}

		p {
			font-family: 'Nunito';
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: -0.025em;
			color: #fff;

			@media (max-width: 480px) {
				font-size: 14px;
			}
		}

		span {
			display: inline-block;
			margin-top: 5px;
			font-weight: 300;
			letter-spacing: -0.025em;
			color: #a9b3c8;

			@media (max-width: 480px) {
				font-size: 14px;
			}
		}
	}
}