.tabs {
	position: relative;
	margin-top: 40px;

	&__list {
		display: flex;
		align-items: center;
		gap: 25px;

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 10px;
		}

		&:not(:last-child) {
			margin-right: 10px;
		}
	}

	&__item {
		@media (max-width: 767px) {
			width: 100%;
		}
	}

	&__btn {
		padding: 15px 35px;
		background-color: #fff;
		border: 1px solid #c3c3c3;
		color: #232323;
		cursor: pointer;
		border-radius: 5px;
		text-align: center;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		font-family: 'Nunito', sans-serif;
		font-size: 18px;
		line-height: calc(24 / 18);
		font-weight: 700;

		@media (max-width: 767px) {
			width: 100%;
		}

		@media (max-width: 575px) {
			font-size: 16px;
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__btn:hover {
		background-color: #162E66;
		color: #fff;
	}

	&__btn--active {
		background-color: #162E66;
		color: #fff;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		pointer-events: none;
	}

	&__content {
		display: none;
		background-color: #fff;
	}

	&__content--active {
		display: block;
	}

	&__top {
		margin-top: 40px;
	}

	&__subtitle {
		font-family: 'Nunito', sans-serif;
		font-size: 21px;
		font-weight: 800;
		color: #232323;
	}

	&__text {
		font-size: 18px;
		line-height: calc(24 / 18);
		color: #6c6c6c;
		margin-top: 15px;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__body {
		display: flex;
		justify-content: space-between;
		gap: 40px;
		margin-top: 30px;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__img {
		width: 100%;
		max-width: 555px;
		height: 545px;

		@media (max-width: 575px) {
			height: 350px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		flex: 0 1 50%;
	}

	&__box+&__box {
		margin-top: 30px;
	}

}