.footer {
	background-color: $color-primary;

	&__content {
		padding: 50px 0;
		display: flex;
		gap: 150px;

		@media (max-width: 1160px) {
			gap: 40px;
		}

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__logo {
		flex-direction: column;
		align-items: flex-start;
		gap: 10px;

		.logo__text {
			color: #cad2e5;
			max-width: 310px;
		}
	}

	&__right {
		display: flex;
		gap: 120px;

		@media (max-width: 1160px) {
			gap: 40px;
		}

		@media (max-width: 991px) {
			gap: 100px;
		}

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 40px;
		}
	}

	&__title {
		color: #fff;
		text-transform: uppercase;
		font-family: 'Nunito', sans-serif;
		font-weight: 700;
		font-size: 18px;
	}

	&__list {
		margin-top: 20px;
		list-style: circle inside;
		color: #cad2e5;

		li {
			a {
				color: #cad2e5;
				transition: all 0.3s ease;

				&:hover {
					color: #fff;
				}
			}
		}

		li+li {
			margin-top: 10px;
		}
	}

	&__tel {
		position: relative;
		display: flex;
		flex-direction: column;
		margin-top: 20px;
		padding-left: 30px;

		&::before {
			content: '';
			background-image: url('../img/svg/tel-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 4px;
			left: 0px;
			width: 20px;
			height: 20px;
		}

		a {
			color: #cad2e5;
			transition: all 0.3s ease;

			&:hover {
				color: #fff;
			}
		}

		a+a {
			margin-top: 8px;
		}
	}

	&__info {
		position: relative;
		display: flex;
		gap: 10px;
		margin-top: 10px;

		@media (max-width: 575px) {
			margin-top: 20px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/tel-2.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 2px;
			width: 20px;
			height: 20px;
		}
	}

	&__desc {
		padding-left: 30px;
		color: #cad2e5;
	}

	&__email {
		position: relative;
		display: inline-block;
		margin-top: 10px;
		padding-left: 30px;
		color: #cad2e5;
		transition: all 0.3s ease;

		@media (max-width: 575px) {
			margin-top: 20px;
		}

		&:hover {
			color: #fff;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/mail.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 0px;
			width: 20px;
			height: 20px;
		}
	}

	&__copy {
		text-align: center;
		color: #cad2e5;
		border-top: 1px solid #2a4074;
		padding: 15px 0;
	}
}

.social-footer {
	display: flex;
	gap: 10px;

	@media (max-width: 575px) {
		flex-direction: column;
	}

	&__link {
		display: flex;
		gap: 5px;
	}

	&__icon {
		border: 1px solid #cad2e5;
		border-radius: 50%;
		background-color: #fff;
		width: 19px;
		height: 19px;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			width: 11px;
			height: 11px;
		}
	}

	span {
		color: #cad2e5;
		transition: all 0.3s ease;

		&:hover {
			color: #fff;
		}
	}
}