.contacts {
	margin-top: 100px;
	padding-bottom: 20px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__caption {
		background-color: $color-primary;
		padding: 100px 15px 150px;

		@media (max-width: 575px) {
			padding-top: 50px;
		}
	}

	&__title {
		color: #fff;
	}

	&__desc {
		font-size: 18px;
		line-height: calc(27 / 18);
		color: #cad2e5;
		margin-top: 20px;
		text-align: center;
	}

	&__box {
		margin-top: 40px;
		text-align: center;
	}

	&__text {
		position: relative;
		display: inline-block;
		font-family: 'Nunito', sans-serif;
		font-size: 29px;
		line-height: calc(61 / 29);
		font-weight: 800;
		text-transform: uppercase;
		text-align: center;
		color: $color-primary;

		@media (max-width: 991px) {
			display: none;
		}

		&::before {
			content: '';
			background-image: url('../img/arrow-down.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 54px;
			right: 170px;
			width: 43px;
			height: 55px;
			animation: translateY 2.5s ease infinite;
		}

		span {
			text-decoration: underline;
		}
	}
}

@keyframes translateY {
	0% {
		transform: translateY(0)
	}

	50% {
		transform: translateY(10px)
	}
}