.about-me {
	margin-top: 130px;

	@media (max-width: 991px) {
		margin-top: 50px;
	}

	&__wrap {
		background-color: $color-primary;
		padding-top: 100px;

		@media (min-width: 992px) {
			padding-bottom: 150px;
		}

		@media (max-width: 991px) {
			padding-top: 50px;
		}
	}

	&__content {
		position: relative;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
			gap: 30px;
		}
	}

	&__info {
		max-width: 640px;

		@media (max-width: 1160px) {
			max-width: 560px;
		}

		@media (max-width: 991px) {
			text-align: center;
		}
	}

	&__title {
		color: #fff;

		@media (min-width: 992px) {
			text-align: left;
		}

		&::before {
			@media (min-width: 992px) {
				left: 0;
				transform: none;
			}
		}
	}

	&__text {
		margin-top: 30px;
		color: #cad2e5;

		@media (min-width: 992px) {
			text-align: justify;
		}

		p {
			font-size: 18px;
			line-height: calc(27 / 18);

			@media (max-width: 575px) {
				font-size: 16px;
			}
		}

		p+p {
			margin-top: 15px;
		}
	}

	&__btn {
		margin-top: 30px;
		border: 1px solid transparent;

		&:hover {
			border-color: #fff;
		}
	}

	&__img {
		@media (min-width: 992px) {
			position: absolute;
			right: 0;
			bottom: -152px;
		}

		@media (max-width: 991px) {
			transform: translateY(3px);
		}

		img {
			@media (max-width: 1160px) {
				width: 350px;
			}

			@media (max-width: 991px) {
				width: 250px;
			}
		}
	}

	&__items {
		display: flex;
		justify-content: space-between;
		gap: 30px;
		position: relative;
		z-index: 1;
		margin-top: -100px;

		@media (max-width: 991px) {
			margin-top: 30px;
			flex-wrap: wrap;
		}
	}

	&__item {
		flex: 0 1 25%;
		background-color: #fff;
		padding: 20px 25px;
		border: 1px solid #c3c3c3;
		border-radius: 5px;
		transition: all 0.3s ease;
		box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);

		&:hover {
			box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
		}

		@media (max-width: 1160px) {
			padding-left: 15px;
			padding-right: 15px;
		}

		@media (max-width: 991px) {
			box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
			flex: 0 1 47%;
		}

		@media (max-width: 575px) {
			flex: 0 1 100%;
		}
	}

	&__icon {
		height: 50px;
	}

	&__subtitle {
		margin-top: 10px;
		font-family: 'Nunito', sans-serif;
		font-size: 16px;
		font-weight: 800;
	}

	&__desc {
		margin-top: 10px;

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

}