.header {
	&__top {
		position: relative;
		background-color: $color-primary;
		padding: 15px 0;
		z-index: 3;
	}

	&__body {
		background-color: #FEF8F8;
		padding: 15px 0;
	}

	&__content {
		@include jcsb;
	}
}

.social-header {
	display: flex;
	position: relative;
	z-index: 2;

	&__icon {
		border: 1px solid $color-primary;
		border-radius: 50%;
		width: 19px;
		height: 19px;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			width: 11px;
			height: 11px;
		}
	}

	&__icon+&__icon {
		margin-left: 5px;
	}
}

.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: #fef8f8;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 170px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fef8f8;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 170px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-bottom: 1px solid #ccc;

				&:first-child {
					border-top: 1px solid #ccc;
				}
			}
		}
	}

	li {
		&:not(:last-child) {
			a {
				&::before {
					@media (min-width: 992px) {
						content: '/';
						line-height: 75px;
						position: absolute;
						right: 0;
						top: 0;
					}
				}
			}
		}
	}

	&__link {
		position: relative;
		text-transform: uppercase;
		padding: 27px 35px 27px 25px;
		font-size: 14px;
		font-family: 'Nunito', sans-serif;
		font-weight: 700;

		@media (max-width: 1160px) {
			padding-left: 20px;
			padding-right: 20px;
		}

		@media (max-width: 991px) {
			display: block;
			padding: 27px 20px 27px 20px;
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35px;
		height: 20px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $color-primary;
		border-radius: 10px;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}

	.menu__icon i {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		width: 50px;
		height: 50px;
		background: transparent;
		border-radius: 7px;
		opacity: 0.5;
		transition: 0.3s ease-out;
	}

