.popup-form {
	position: relative;
	display: none;
	width: 470px;
	height: auto;
	padding: 75px 25px 50px;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border: 1px solid #c3c3c3;
	background: #fff;
	z-index: 9999;
	border-radius: 5px;

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		text-align: center
	}

	&__text h4 {
		font-size: 26px;
		margin-bottom: 15px
	}

	&__text p {
		font-size: 16px;
		line-height: 1.2
	}

	&__text span {
		color: red;
		font-size: 19px;
		line-height: 1.2
	}

	&__accept {
		width: 100px;
		height: 50px;
		font-size: 18px;
		margin-top: 40px;
		background-color: $color-primary;
		color: #fff;
	}

	&__close {
		position: absolute;
		height: 25px;
		width: 25px;
		top: 10px;
		right: 10px;
		margin-top: 0
	}
}