html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320px;
	font-family: "SourceSansPro", sans-serif;
	color: #1E1E1E;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #6c6c6c;
	transition: all 0.3s ease;

	&:hover {
		color: $color-primary;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1160px;

	@media (max-width: 1160px) {
		max-width: 970px;
	}

	@media (max-width: 991px) {
		max-width: 750px;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	position: relative;
	padding-bottom: 20px;
	font-family: 'Nunito', sans-serif;
	font-weight: 800;
	font-size: 38px;
	text-align: center;
	text-transform: uppercase;
	color: #232323;

	&::before {
		content: '';
		width: 40px;
		height: 4px;
		border-radius: 10px;
		background: #c3c3c3;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}

	@media (max-width: 991px) {
		font-size: 34px;
	}

	@media (max-width: 575px) {
		font-size: 22px;
	}
}