.services {
	padding-top: 100px;

	@media (max-width: 575px) {
		padding-top: 50px;
	}

	&__content {
		margin-top: 50px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-wrap: wrap;
			gap: 40px;
		}
	}

	&__item {
		flex: 0 1 30%;
		text-align: center;

		@media (max-width: 991px) {
			flex: 0 1 45%;
		}

		@media (max-width: 575px) {
			flex: 0 1 100%;
		}
	}

	&__img {
		min-height: 135px;
	}

	&__title {
		margin-top: 20px;
		font-family: 'Nunito', sans-serif;
		font-size: 22px;
		font-weight: 700;

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__desc {
		margin-top: 20px;
		font-size: 18px;
		line-height: calc(27 / 18);
		color: #555;

		@media (max-width: 575px) {
			margin-top: 10px;
			font-size: 16px;
		}
	}
}