.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	transition: all 0.3s ease-in-out;
	text-align: center;
	padding: 15px 30px;
	background-color: #fff;
	color: $color-primary;
	text-transform: uppercase;
	border-radius: 5px;
	transform: perspective(1px) translateZ(0);
	transition-property: color;
	transition-duration: 0.3s;

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $color-primary;
		transform: scaleX(0);
		transform-origin: 0 50%;
		transition-property: transform;
		transition-duration: 0.3s;
		transition-timing-function: ease-out;
	}

	&:hover:before {
		transform: scaleX(1);
	}

	&:hover {
		color: #fff;
	}

	&--animate {
		height: 250px;
		width: 250px;
		position: absolute;
		top: -20px;
		left: -120px;
		background: $color-primary;
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}

	@keyframes ripple {
		20% {
			opacity: 0;
			transform: scale(2.5);
		}

		100% {
			opacity: 0;
			transform: scale(2.5);
		}
	}
}

.btn+.btn {
	margin-left: 15px;

	@media (max-width: 991px) {
		margin: 0;
	}
}