.lang {
	display: flex;
	align-items: center;
	gap: 10px;

	@media (max-width: 991px) {
		justify-content: center;
	}

	&__text {
		color: #fff;

		@media (max-width: 390px) {
			font-size: 12px;
		}
	}

	&__content {
		position: relative;
	}

	&__content--active {
		.lang__list {
			opacity: 1;
			visibility: visible;
			top: 34px;
		}

		.lang__link--arrow::before {
			transform: translateY(-50%) rotate(180deg);
		}
	}

	&__box {}

	&__link {
		display: flex;
		align-items: center;
		gap: 7px;
		padding: 0 9px;

		@media (max-width: 390px) {
			font-size: 14px;
		}

		&--arrow {
			&::before {
				content: '';
				position: absolute;
				top: 50%;
				right: -13px;
				transform: translateY(-50%);
				background-image: url('../img/svg/arrow-down.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: 100%;
				width: 16px;
				height: 16px;
				transition: all .3s ease;
			}
		}

		span {
			color: #fff;
			transition: all 0.3s ease;
		}
	}

	&__img {
		width: 20px;
		height: 20px;
	}

	&__list {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 40px;
		left: 0;
		background: $color-primary;
		border-radius: 0 0 5px 5px;
		z-index: 6;
		transition: all 0.3s ease;
	}

	&__item {
		.lang__link {
			padding-bottom: 3px;

			&:hover {
				span {
					color: #cad2e5;
				}
			}
		}
	}
}

.arrow-down {}